@import url(https://fonts.googleapis.com/css?family=Roboto:100&display=swap);
.App {
  font-family: Roboto, sans-serif;
  line-height: 1.5em; }

.App-header h1, .App-header p {
  padding: 0 1em;
  margin-bottom: 1em; }

.App-header a {
  color: #333333; }

.App-image {
  background-image: url(/BRUL/selected-works2/static/media/render.b9f58e74.jpg);
  background-position: center;
  background-size: cover;
  width: 100vw;
  height: 100vh; }

@media screen and (min-width: 768px) {
  .App {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;
    align-content: stretch; }
  .App-header {
    max-width: 30vw; }
  .App-image {
    height: 100vh;
    width: 70vw; } }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

